.founders-container {
  /* height: 100%; */
  width: 100%;
  text-align: center;
  /* background-image: linear-gradient(to right bottom, #ff7979, #4834d4); */
}

.flex__cards {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-flow: column wrap;
  padding: 3.5rem 1rem;
}

.heading__founders {
  font-size: 4rem;
  padding: 1rem;
  width: 40rem;
  background: black;
  text-transform: uppercase;
  font-weight: 600;
  margin-bottom: 3rem;
  display: inline-block;
  border-radius: 2rem;
  box-shadow: 0.2rem 0.7rem 1rem rgb(0 0 0 / 20%);
}

.heading__founders.founders.glitch {
  color: #dfbfbf;
  position: relative;
}
.glitch::before {
  border-radius: 2rem;
  background: black;
  content: attr(data-text);
  position: absolute;
  padding: 1rem;
  left: 2px;
  text-shadow: -5px 0 magenta;
  overflow: hidden;
  top: 0;
  display: block;
  animation: noise-1 3s linear infinite alternate-reverse;
}
.glitch::after {
  border-radius: 2rem;
  background: black;
  content: attr(data-text);
  position: absolute;
  padding: 1rem;
  left: 2px;
  text-shadow: -5px 0 lightgreen;
  /* background: black; */
  overflow: hidden;
  top: 0;
  display: block;
  animation: noise-2 3s linear infinite alternate-reverse;
}
